@import '../../style/style.scss';

.project-teaser-container {
    display: flex;
    height: fit-content;

    h3 {
        text-align: left;
        margin-bottom: 10px;
    }

    p {
        color: $textLight;
        text-align: left;
        line-height: 28px;
        letter-spacing: 0.1px;
    }

    .project-teaser-text-container {
        display: flex;
        width: 100%;
        align-items: flex-start;
        flex-direction: column;
        justify-content: space-between;
        gap: 24px;

        @include m {
            width: 50%;
        }
    }

    .chip-row-container {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: flex-start;
        padding: 0px;
        gap: 10px;

        @include xl {
            padding: 0px 0px;
            width: 560px;
        }
    }

    .project-teaser-image {
        transition: box-shadow 400ms ease-out 50ms;
        cursor: pointer;
            display: flex;
            width: 100%;
            align-items: flex-start;
            flex-direction: column;
            justify-content: space-between;
            background-color: $primaryBackground;
        

        &:hover {
            -webkit-box-shadow: 5px 5px 15px 2px rgba(0, 0, 0, 0.2);
            box-shadow: 5px 5px 15px 2px rgba(0, 0, 0, 0.2);

        }
    }
}

.project-featured {
    &.project-teaser-container {
        width: 100%;
        align-items: flex-start;
        flex-direction: column;
        justify-content: space-between;
        gap: 40px;
        margin-bottom: 40px;

        @include m {
            flex-direction: row;
            margin-bottom: 100px;
            gap: 100px;
        }

        @include xl {
            padding: 0px 0px;
            width: 1200px;
        }

        .project-teaser-text-container {
            display: flex;
            width: 100%;
            align-items: flex-start;
            flex-direction: column;
            justify-content: space-between;
            gap: 24px;

            @include m {
                width: 50%;
            }
        }

        .project-teaser-image-container {
            display: flex;
            width: 100%;
            height: 40vw;
            align-items: flex-start;
            flex-direction: column;
            justify-content: space-between;
            background-color: $primaryBackground;
            gap: 24px;

            @include m {
                height: 24vw;
                width: 50%;
            }

            @include xl {
                padding: 0px 0px;
                width: 560px;
                height: 370px;
            }
        }

    }

}

.project-additional{
    &.project-teaser-container {
        width: 100%;
        align-items: flex-start;
        flex-direction: column;
        justify-content: space-between;
        gap: 40px;
        margin-bottom: 40px;

        @include s {
            width: 40%; 
        }

        @include m {

            width: 40%;
        }

        @include xl {
            padding: 0px 0px;
            width: 450px;
        }
    }

    .project-teaser-text-container {
        display: flex;
        width: 100%;
        align-items: flex-start;
        flex-direction: column;
        justify-content: space-between;
        gap: 24px;

        @include m {
            width: 100%;
        }


    }


    .project-teaser-image {
        display: flex;
        width: 100%;
        aspect-ratio: 1 / 1;
        align-items: flex-start;
        flex-direction: column;
        justify-content: space-between;
        background-color: $primaryBackground;
    }

    @include xl {
        padding: 0px 0px;
        width: 560px;
    }
    }